@import url(https://fonts.googleapis.com/css?family=Oxygen:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Poppins|Roboto|Rubik);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* 
orange: #E05915 
purple: #431C5D
white: #E6E9F0
light-blue: #BCCBDE
light-blue#2 : #C2DDE6
red: #E01515
*/

html,
body  {
  font-size: 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 700;
}

h2 {
  font-size: 3.5rem;
  font-weight: 700;
}

h4 {
  font-size: 2.5rem;
  line-height: 3rem;
}

h5 {
    font-size: 2rem; 
}

p {
  font-size: 2rem;
  line-height: 3rem;
}

.cursor {
  cursor: pointer;
}

.btn {
  width: 140px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  font-size: 2rem;
  color: #ffffff;
  border: 0;
}

.purple {
  background-color: #431c5d;
}

.orange {
  background-color: #e05915;
}

.white {
  background-color: #e6e9f0;
}

.light-blue {
  background-color: #bccbde;
}

.light-blue-2 {
  background-color: #C2DDE6;
}

.red {
  background-color: #e01515;
}

.padding {
  padding: 65px 80px;
}

.dispay-none {
  display: none;
}

.f700 {
  font-weight: 700;
}

.f400 {
  font-weight: 400;  
}

.overflow {
  overflow: hidden;
}

.right-align {
  text-align: right;
}

.pageStructure {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.container {
  margin-top: 5vh;
  width: 960px;
  height: 500px;
  display: flex;
}

.half-container {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.half-container {
    color: #431C5D;
}

.text-home-center {
    margin: 40px 0; 
}

.home-right {
    display: flex;
    align-items: center;
}

.home-right img {
    align-self: flex-end;
    height: 500px;
}


.profile-page-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80%;
}

.profile-page-image > div {
  height: 302px;
  width: 302px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; 
}

.profile-page-btn {
  display: flex;
  flex-direction: column;
}

.profile-btn-line {
  display: flex;
  margin-top: 20px;
  width: 300px;
  justify-content: space-between;
}

.photo-profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-list li {
  font-size: 2rem;
  margin-top: 20px;
}

.input-edit-profile {
    display: flex;
    flex-direction: column; 
    overflow-x: scroll;
    width: 100%;
    height: 60%;
}
.form-auth {
    margin: 30px 0;
}

.input-auth {
  /* width: 100%; */
  background: none;
  border: 2px solid #bccbde;
  font-size: 2rem;
  padding: 10px 20px;
  color: #431c5d;
  margin-bottom: 15px;
  font-weight: 300;
}

.input-sign-up {
    display: flex;
    flex-direction: column; 
    overflow-x: scroll;
    width: 100%;
    height: 50%;
}

.input-login {
    display: flex;
    flex-direction: column; 
    width: 100%;
}

.form-auth input[type=submit]{
    margin-top: 10px;
}

.input-auth::-webkit-input-placeholder {
  color: #431c5d;
  opacity: 0.4;
}

.link-auth-text {
    display: flex;
    justify-content: center;
    align-self: flex-end;
}

.link-auth {
    font-weight: 700;
}

.logoAuth {
    align-self: flex-end;
}

.logoAuth img{
    width: 280px;
}   

.auth-right {
    justify-content: space-between;
}
/* 
orange: #E05915 
purple: #431C5D
*/

nav {
  font-family: "Oxygen", sans-serif;
  font-size: 2rem;
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: center;
  background-color: rgba(255, 255, 255, 1);
}

.navbar h2 {
  font-weight: 700;
  color: #e05915;
  font-size: 2.5rem;
}

.navbar ul {
  display: flex;
  align-items: center;
}

.navbar ul li {
  margin-left: 32px;
  color: #431c5d;
}

.navbar {
  align-items: center;
  display: flex;
  justify-content: space-between;
  height: 10vh;
  width: 960px;
}

.link-photo-profile {
  display: flex;
  align-items: center;
}

.link-photo-profile > div {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin-left: 8px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.selector-right{
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.selector-right-container img {
    height: 400px;
}

.price-selector {
    margin-top: 20px;
}
.cart-box {
    flex-direction: column;
    justify-content: space-between;
    color: #431C5D;
}

.cartContainer {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 450px;
}

.cart-product {
    display: grid;
    grid-gap: 5px;
    grid-template-columns: repeat(8, 1fr); 
    background-color: #BCCBDE;
    border-radius: 10px;
    justify-content: space-between;
    align-items: center;  
    padding: 10px 20px;
    margin-bottom: 10px;
}


.form-cart {
    display: flex;
    align-items: center;
}

.label-design-name {
    font-size: 2rem; 
}

.input-design-name {
    width: 50px;
    text-align: right;
    background: #E6E9F0;
    border: 1px solid #431C5D;
    font-size: 1.4rem;
    padding: 3px 5px;
    margin: 0 5px;
}

.btn-quatity {
    color: #ffffff;
    font-size: 1.4rem;
    height: 30px;
    width: 50px;
    border-radius:5px; 
}

.btn-cart {
    font-size: 2rem;
    color: #ffffff;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    border-radius:5px; 
    border: 0;
}

.cart-toPay {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nameDesignList {
    grid-column: span 3;
}

.cart-type {
    grid-column: span 2;
}
.padding-canvas {
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.control-structure {
  height: 300px;
  overflow-y: auto;
  border-radius: 10px;
}

.controles-box {
  font-size: 1.4rem;
  padding: 10px 20px;
  border-radius: 10px;
}

.colors-table {
  padding: 15px;
  border-radius:10px; 
  display: grid;
  grid-template-columns: repeat(23, 1fr);
  grid-gap: 1px;
}

.color-cube {
  height: 15px;
}

.selected-color {
  box-sizing: border-box;
  border: 2px solid #ffffff;
}

.controles-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 10px;
  font-size: 1.4rem;
  padding: 15px 20px;
  border-radius: 10px;
  margin-top: 10px;
}

.controles-inside {
  border-radius: 10px;
  padding: 15px 20px 10px 20px;
}

.btn-control-box {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-gap: 10px;
}

.btn-control-box .control-input {
  grid-column: span 8;
}

.control-input {
  background: #e6e9f0;
  border: 1px solid#431C5D;
  color: #431c5d;
  padding: 3px 5px;
}
.control-input::-webkit-input-placeholder { 
  color: #431c5d;
}

.btn-control {
  width: 41px;
  height: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 1.4rem;
  color: #ffffff;
  border: 0;
}

.range-control {
  margin-top: 10px;
  display: grid;
  grid-template-columns: auto 100px auto 100px;
  grid-template-rows: repeat(2, auto);
  grid-gap: 5px;
  align-items: center;
}

.btn-control-text-box {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-gap: 10px;
}

.btn-control-text-box .control-input {
  grid-column: span 9;
}

.range-control-text-rotation {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 10px;
  margin-top: 8px;
}

.itm {
  background-color: #C2DDE6;
}

.input-control {
  background: #e6e9f0;
  border: 1px solid#431C5D;
  color: #431c5d;
  padding: 3px 5px;
  flex-grow: 2;
}

.bottom {
  margin-bottom: 5px;
}

.text-control-inside {
  display: flex;
  align-items: center;
}

.text-control-inside label {
  margin-right: 5px;
}

.text-control-inside input {
  text-align: right;
  width: 100px;
}

.text-control {
  display: flex;
  justify-content: space-between;
}

.text-control > * {
  margin-right: 10px;
}

.text-control select {
  background: #e6e9f0;
  color: #431c5d;
  width: 100px;
}

.text-size-control {
  background: #e6e9f0;
  border: 1px solid#431C5D;
  color: #431c5d;
  width: 100px;
  padding: 3px 5px;
}

.demo-advisor {
  background-color: rgba(244, 89, 21, 0.7);
  height: 60px;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 10px;
}

.label-design-name {
  font-size: 2rem;
}

.input-design-name-canvas {
  margin-left: 20px;
  background: #e6e9f0;
  border: 1px solid #431c5d;
  font-size: 1.4rem;
  padding: 5px 10px;
  flex-grow: 2;
}

.form-save-design {
  display: flex;
  flex-direction: column;
}

.form-inside-design {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.layerShow {
  opacity: 1;
  display: block;
}

.layerHidden {
  opacity: 0;
  display: none;
}

.canvas-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
